import uniLoadMore from '@/components/uni-load-more.vue';
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			loadingText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			},
			scrollLeft: 0,
			isClickChange: false,
			tabIndex: 0,
			listData: [],
			data0: {
				"datetime": "40分钟前",
				"article_type": 0,
				"title": "uni-app行业峰会频频亮相，开发者反响热烈!",
				"source": "DCloud",
				"comment_count": 639
			},
			data1: {
				"datetime": "一天前",
				"article_type": 1,
				"title": "DCloud完成B2轮融资，uni-app震撼发布!",
				"image_url": "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/shuijiao.jpg?imageView2/3/w/200/h/100/q/90",
				"source": "DCloud",
				"comment_count": 11395
			},
			data2: {
				"datetime": "一天前",
				"article_type": 2,
				"title": "中国技术界小奇迹：HBuilder开发者突破200万",
				"image_url": "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/muwu.jpg?imageView2/3/w/200/h/100/q/90",
				"source": "DCloud",
				"comment_count": 11395
			},
			data4: {
				"datetime": "2小时前",
				"article_type": 4,
				"title": "uni-app 支持原生小程序自定义组件，更开放、更自由",
				"image_url": "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/cbd.jpg?imageView2/3/w/200/h/100/q/90",
				"source": "DCloud",
				"comment_count": 69
			},
			data3: {
				"article_type": 3,
				"image_list": [{
					"url": "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/cbd.jpg?imageView2/3/w/200/h/100/q/90",
					"width": 563,
					"height": 316
				}, {
					"url": "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/muwu.jpg?imageView2/3/w/200/h/100/q/90",
					"width": 641,
					"height": 360
				}, {
					"url": "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/shuijiao.jpg?imageView2/3/w/200/h/100/q/90",
					"width": 640,
					"height": 360
				}],
				"datetime": "5分钟前",
				"title": "uni-app 支持使用 npm 安装第三方包，生态更趋丰富",
				"source": "DCloud",
				"comment_count": 11
			},
			tabBars: [{
				name: '全部',
				id: 'quanbu'
			}, {
				name: '未付款',
				id: 'weifukuan'
			}, {
				name: '已完成',
				id: 'yiwancheng'
			}]
		}
	},
	onLoad: function() {
		this.listData = this.randomfn()
	},
	methods: {
		goDetail(e) {
			uni.navigateTo({
				url: '/pages/template/tabbar/detail/detail?data=' + e.title
			})
		},
		close(index1, index2) {
			uni.showModal({
				content: '是否删除本条信息？',
				success: (res) => {
					if (res.confirm) {
						this.listData[index1].data.splice(index2, 1);
					}
				}
			})
		},
		loadMore(e) {
			this.listData[e].loadingType = 1;
			setTimeout(() => {
				this.addData(e);
			}, 1200);
		},
		addData(e) {
			if (this.listData[e].data.length > 30) {
				this.listData[e].loadingType = 2;
				return;
			}
			for (let i = 1; i <= 10; i++) {
				this.listData[e].data.push(this['data' + Math.floor(Math.random() * 5)]);
			}
			this.listData[e].loadingType = 1;
		},
		async changeTab(e) {
			let index = e.detail.current;
			if (this.isClickChange) {
				this.tabIndex = index;
				this.isClickChange = false;
				return;
			}

			this.isClickChange = false;
			this.tabIndex = index; //一旦访问data就会出问题
		},
		async tapTab(index) { //点击tab-bar
			if (this.tabIndex === index) {
				return false;
			} else {
				this.isClickChange = true;
				this.tabIndex = index;
			}
		},
		randomfn() {
			let ary = [];
			for (let i = 0, length = this.tabBars.length; i < length; i++) {
				let aryItem = {
					loadingType: 0,
					data: []
				};
				for (let j = 1; j <= 10; j++) {
					aryItem.data.push(this['data' + Math.floor(Math.random() * 5)]);
				}
				ary.push(aryItem);
			}
			return ary;
		}
	}
}
